<template>
  <div class="min-h-screen bg-gray-100 flex justify-center items-center">
    <div class="w-full max-w-md p-6 bg-white rounded-lg shadow-lg">
      <h2 class="text-3xl font-semibold text-center text-blue-600 mb-6">Random Joke Generator</h2>

      <!-- Joke Display Section -->
      <div v-if="loading" class="text-center text-lg text-blue-600">
        Loading joke...
      </div>

      <div v-else-if="error" class="text-center text-lg text-red-600">
        Oops! Something went wrong. Try again.
      </div>

      <div v-else>
        <p class="text-center text-xl text-gray-700">{{ joke }}</p>
        <button
          @click="getNewJoke"
          class="mt-6 w-full py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
        >
          Get Another Joke
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      joke: "",        // Store the current joke
      loading: false,  // Handle loading state
      error: false,    // Handle error state
    };
  },
  methods: {
    async getNewJoke() {
      this.loading = true;
      this.error = false;
      
      try {
        const response = await fetch("https://v2.jokeapi.dev/joke/Any?type=single");
        const data = await response.json();

        if (data.error) {
          throw new Error("Failed to fetch joke");
        }

        this.joke = data.joke;
      } catch (err) {
        this.error = true;
        this.joke = "";
      } finally {
        this.loading = false;
      }
    },
  },
  mounted() {
    // Fetch a joke when the component is mounted
    this.getNewJoke();
  },
};
</script>

<style scoped>
/* Styling for Joke Generator App */
</style>
