<template>
  <div class="min-h-screen bg-white flex justify-center items-center">
    <div class="w-full max-w-md bg-white rounded-lg shadow-lg p-6 sm:p-8">
      <h2 class="text-3xl font-semibold text-center text-gray-800 mb-6">To-Do List</h2>

      <!-- Input Section -->
      <div class="flex mb-6">
        <input
          v-model="newTask"
          placeholder="Add a new task"
          class="w-full px-4 py-2 rounded-l-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 transition-all"
        />
        <button
          @click="addTask"
          class="px-6 py-2 bg-indigo-600 text-white rounded-r-lg hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 transition-all"
        >
          Add
        </button>
      </div>

      <!-- No Tasks Message -->
      <div v-if="tasks.length === 0" class="text-center text-gray-500 italic">
        No tasks yet!
      </div>

      <!-- Task List -->
      <ul v-else class="space-y-4">
        <li
          v-for="(task, index) in tasks"
          :key="index"
          class="flex justify-between items-center bg-gray-100 p-4 rounded-lg shadow-sm hover:bg-gray-200 transition-all"
          :class="{ 'line-through text-gray-500': task.completed }"
        >
          <span
            @click="toggleTask(index)"
            class="cursor-pointer text-lg text-gray-800 hover:text-indigo-600 transition-all"
          >
            {{ task.text }}
          </span>
          <button
            @click="removeTask(index)"
            class="bg-red-500 text-white px-3 py-1 rounded-full hover:bg-red-600 focus:outline-none focus:ring-2 focus:ring-red-500 transition-all"
          >
            Remove
          </button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newTask: "",
      tasks: JSON.parse(localStorage.getItem("tasks")) || [],
    };
  },
  methods: {
    addTask() {
      if (this.newTask.trim()) {
        this.tasks.push({ text: this.newTask.trim(), completed: false });
        this.newTask = "";
        this.saveTasks();
      }
    },
    removeTask(index) {
      this.tasks.splice(index, 1);
      this.saveTasks();
    },
    toggleTask(index) {
      this.tasks[index].completed = !this.tasks[index].completed;
      this.saveTasks();
    },
    saveTasks() {
      localStorage.setItem("tasks", JSON.stringify(this.tasks));
    },
  },
};
</script>

<style scoped>
/* Add any custom styling you want */
</style>
