import { createRouter, createWebHistory } from 'vue-router';

import ToDoApp from '../components/TodoApp.vue';  // Import your To-Do App component
import JokeGenerator from '../components/JokeGenerator.vue';  // Import Recipe Finder component

const routes = [

  { path: '/todo', component: ToDoApp, name: 'todo' },  // Route for To-Do App
  { path: '/joke', component: JokeGenerator, name: 'joke' },  // Route for Recipe Finder
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
